import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';
import CheckBox from './CheckBox';
import { ReactComponent as CloseBtn } from '../images/bottomsheet/close_btn.svg';
import CopyBtn from './CopyBtn';
import Modal from './Modal';
import { ReactComponent as Namu } from '../images/account/icon_namu.svg';
import { ReactComponent as Kb } from '../images/account/icon_kb.svg';
import { ReactComponent as Sk } from '../images/account/icon_sk.svg';
import { ReactComponent as Shinhan } from '../images/account/icon_shinhan.svg';
import { ReactComponent as Next } from '../images/account/btn_next.svg';
import KakaoBtn from './Kakao/KakaoBtn';

const BottomSheet = ({ onClose, type, onClick }) => {
  const Container = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.86);
    z-index: ${(props) => props.z};
    bottom: 0;
  `;
  const Contents = styled.div`
    background: #ffffff;
    width: 100%;
    height: ${(props) => props.height || 'auto'};
    margin-top: 13vw;
    bottom: 0;
    border-radius: 15px 15px 0px 0px;
    position: absolute;
  `;
  const CloseBox = styled.div`
    display: flex;
    justify-content: end;
    padding: 3vw 4vw 0 0;
    cursor: pointer;
  `;
  const [copyModal, setCopyModal] = useState(false);

  return (
    <>
      {type === 'personalAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents
              onClick={(e) => e.stopPropagation()}
              height={`calc(100vh - 13vw)`}
            >
              <CloseBox>
                <CloseBtn width="6vw" onClick={() => onClose(false)} />
              </CloseBox>
              <PersonalAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'marketingAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents
              onClick={(e) => e.stopPropagation()}
              height={`calc(100% - 13vw)`}
            >
              <CloseBox>
                <CloseBtn width="6vw" onClick={() => onClose(false)} />
              </CloseBox>
              <MarketingAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'share' && (
        <>
          {copyModal && (
            <Modal type="copy" onClose={() => setCopyModal(false)} />
          )}
          <Container z={30} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <ShareDetail setCopyModal={setCopyModal} />
            </Contents>
          </Container>
        </>
      )}
      {type === 'agree' && (
        <>
          <Container z={10} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <AgreeDetail onClick={onClick} />
            </Contents>
          </Container>
        </>
      )}
    </>
  );
};

export default BottomSheet;

const AgreeDetail = ({ onClick }) => {
  const [agreeItems, setAgreeItems] = useState([
    { id: 0, name: '[] ()  ' },
    {
      id: 1,
      name: '[]      ',
    },
  ]);

  const H3 = styled.h3`
    font-size: 5vw;
    line-height: 150%;
  `;
  const [readMe, setReadMe] = useState(null);
  return (
    <>
      {readMe === 0 ? (
        <BottomSheet type="personalAgree" onClose={() => setReadMe(null)} />
      ) : null}
      {readMe === 1 ? (
        <BottomSheet type="marketingAgree" onClose={() => setReadMe(null)} />
      ) : null}

      <H3>
            
        <br />   .
      </H3>
      <CheckBox
        onClick={onClick}
        agreeItems={agreeItems}
        setAgreeItems={setAgreeItems}
        setReadMe={setReadMe}
      />
    </>
  );
};

const ShareDetail = ({ setCopyModal }) => {
  const [haveAccount, setHaveAccount] = useState(null);
  const [accountList, setAccountList] = useState([
    { id: 0, name: '', status: 1, checked: true },
    {
      id: 1,
      name: '',
      status: 0,
    },
    {
      id: 2,
      name: 'KB',
      status: 0,
    },
    {
      id: 3,
      name: 'SK',
      status: 2,
    },
  ]);
  useEffect(() => {
    const haveAccountArray = accountList.filter((item) => item.status === 0);
    setHaveAccount(haveAccountArray.length > 0);
    console.log('   : ', haveAccountArray.length);
  }, [accountList]);
  console.log('   boolean : ', haveAccount);
  const BtnBox = styled.div`
    display: flex;
    gap: 2vw;
    margin: 2vw;
  `;
  const Title = styled.div`
    display: flex;
    gap: 4vw;
    align-items: center;
    p {
      margin: 0;
      font-size: 4vw;
      font-weight: 700;
    }
  `;
  const AccountBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 3vw;
    align-items: center;
    cursor: pointer;
    input {
      &[type='radio'] {
        cursor: pointer;
        accent-color: #fe5000;
        width: 6vw;
        height: 6vw;
      }
    }
    label {
      cursor: pointer;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  `;
  const AccountContainer = styled.div`
    margin: 9vw 0;
  `;
  const Container = styled.div`
    h3 {
      margin: 6vw 0 0 0;
      font-size: 5vw;
      line-height: 150%;
    }
    h4 {
      margin: 2vw 0 0 0;
      font-size: 3.7vw;
      color: #757575;
      font-weight: 400;
    }
  `;

  const CreateBtn = styled.button`
    display: flex;
    align-items: center;
    gap: 2vw;
    cursor: pointer;
    height: 8.5vw;
    font-size: 4vw;
    font-weight: 700;
    padding: 2vw 3vw;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 2vw;
    background-color: #fe5000;
    border: none;
    &:hover {
      background-color: #cc4400;
    }
    &:active {
      background-color: #cc4400;
    }
  `;

  const handleChange = (e) => {
    const { id, value } = e.target;
    console.log('   :', value);
    console.log('  id :', id);
  };

  return (
    <Container>
      {haveAccount ? (
        <h3>
           
          <br />  
        </h3>
      ) : (
        <h3>
            <br />
            
        </h3>
      )}

      <h4>    </h4>
      <AccountContainer>
        {accountList.map((account) => (
          <div key={account.id}>
            {account.status === 0 && (
              <AccountBox key={account.id}>
                <label htmlFor={account.id} onClick={handleChange}>
                  <Title>
                    {account.id === 0 && <Namu width="10vw" />}
                    {account.id === 1 && <Shinhan width="10vw" />}
                    {account.id === 2 && <Kb width="10vw" />}
                    {account.id === 3 && <Sk width="10vw" />}
                    <p>{account.name}</p>
                  </Title>
                  <input
                    type="radio"
                    name="account"
                    value={account.name}
                    onChange={handleChange}
                    id={account.id}
                    checked={account.checked}
                  />
                </label>
              </AccountBox>
            )}

            {account.status === 1 && (
              <AccountBox key={account.id}>
                <Title>
                  {account.id === 0 && <Namu width="10vw" />}
                  {account.id === 1 && <Shinhan width="10vw" />}
                  {account.id === 2 && <Kb width="10vw" />}
                  {account.id === 3 && <Sk width="10vw" />}
                  <p>{account.name}</p>
                </Title>
                <CreateBtn
                  onClick={() => {
                    console.log('   :', account.name);
                    console.log('  id :', account.id);
                  }}
                >
                  <div></div>
                  <div>
                    <Next width="2vw" />
                  </div>
                </CreateBtn>
              </AccountBox>
            )}
            {account.status === 2 && (
              <AccountBox key={account.id}>
                <Title>
                  {account.id === 0 && <Namu width="10vw" />}
                  {account.id === 1 && <Shinhan width="10vw" />}
                  {account.id === 2 && <Kb width="10vw" />}
                  {account.id === 3 && <Sk width="10vw" />}
                  <p>{account.name}</p>
                </Title>
                <CreateBtn
                  onClick={() => {
                    console.log('   :', account.name);
                    console.log('  id :', account.id);
                  }}
                >
                  <div>   </div>
                  <div>
                    <Next width="2vw" />
                  </div>
                </CreateBtn>
              </AccountBox>
            )}
          </div>
        ))}
      </AccountContainer>
      {haveAccount ? (
        <BtnBox>
          <CopyBtn active={true} setCopyModal={setCopyModal} />
          <KakaoBtn active={true} />
        </BtnBox>
      ) : (
        <BtnBox>
          <CopyBtn active={false} setCopyModal={setCopyModal} />
          <KakaoBtn active={false} />
        </BtnBox>
      )}
    </Container>
  );
};

